    Username: <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>input</span> <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>text<span class="token punctuation">"</span></span>     <span class="token attr-name">name</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>username<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
    Password: <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>input</span> <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>password<span class="token punctuation">"</span></span> <span class="token attr-name">name</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>password<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
              <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>input</span> <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>submit<span class="token punctuation">"</span></span>   <span class="token attr-name">value</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>Sign in!<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>form</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p>Here is the code on the server handling the HTTP request sent when the form is submitted:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>app<span class="token punctuation">.</span><span class="token function">post</span><span class="token punctuation">(</span><span class="token string">'/login'</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">request<span class="token punctuation">,</span> response</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
  <span class="token keyword">const</span> username <span class="token operator">=</span> request<span class="token punctuation">.</span>body<span class="token punctuation">.</span>username
  <span class="token keyword">const</span> password <span class="token operator">=</span> request<span class="token punctuation">.</span>body<span class="token punctuation">.</span>password
  <span class="token comment">// Query to be sent to the database to check if password and username are correct.</span>
  <span class="token keyword">const</span> query <span class="token operator">=</span> <span class="token string">'SELECT id FROM members WHERE username = "'</span><span class="token operator">+</span>username<span class="token operator">+</span><span class="token string">'" AND password = "'</span><span class="token operator">+</span>password<span class="token operator">+</span><span class="token string">'"'</span>
  <span class="token comment">// Query sent to database, the id of the user is retrieved, etc.</span>
  <span class="token comment">// ...</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><p>As your answer, write a username and a password you can use to login as the member having the password <code>abc123</code>, no matter which username that member has (this can be used to see if a member/which member that has that password).</p>
<p>Be explicit about what you enter as username and what you enter as password.</p>
<p><strong>Note</strong>: As you can see in the code, passwords are stored as plain text in the database.</p>
</template>
</ExamQuestion>
