<ExamQuestion>
<template v-slot:question>
<p>This is a question about GDPR. The scenario is that you are the owner of a website, and you want to collect the IP addresses of your visitors, so you can see how many unique visitors you have. Which of the statements below are true for you to comply with GDPR?</p>
<ul>
<li>It's OK for me to collect the IP addresses of my visitors without informing them about it.</li>
<li>It's OK for me to collect the IP addresses of my visitors as long as I inform them about it in advance.</li>
<li>I have to ask the visitors if I may collect their IP addresses, and I may only collect the IP addresses from the visitors that say yes. If they later regret their decision and say no I have to delete their IP addresses.</li>
<li>I can start collecting the IP addresses of my visitors and then ask my visitors if I may collect their IP addresses, but I have to delete the IP addresses from the visitors that say no.</li>
<li>I have to ask the visitors if I may collect their IP addresses, and I may only collect the IP addresses from the visitors that say yes. If some visitors later regret their decision and say no I don't have to delete their IP addresses, because I had their permission to collect them when I collected them.</li>
</ul>
<p>You'll get:</p>
