<h2 id="question-21-2p" tabindex="-1"><a class="header-anchor" href="#question-21-2p" aria-hidden="true">#</a> Question 21 (2p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>In JavaScript, implement the synchronous function <code>duplicateString(string)</code>, which receives a string as argument and returns a string containing that string twice, e.g. <code>duplicateString('ab')</code> ➔ <code>abab</code>.</p>
<p>Then implement the synchronous function <code>duplicateStringTwice(string)</code>, which receives a string as argument and returns a string containing that string four times, e.g. <code>duplicateStringTwice('ab')</code> ➔ <code>abababab</code>. The only allowed operation to use in the implementation of <code>duplicateStringTwice(string)</code> is function calls to <code>duplicateString(string)</code>.</p>
<p>Then implement asynchronous versions of these two functions using callback functions instead of return values. The asynchronous version of <code>duplicateStringTwice</code> may only be implemented using the asynchronous version of <code>duplicateString</code>, no other operations are allowed.</p>
</template>
</ExamQuestion>
